import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Tool from '@/components/Tool'
import Forum from '@/components/Forum'
import Shop from '@/components/Shop'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },{
    	path: '/tool',
      name: 'Tool',
      component: Tool
    },{
    	path: '/forum',
      name: 'Forum',
      component: Forum
    },{
    	path: '/shop',
      name: 'Shop',
      component: Shop
    }
  ]
})



